.vbox-overlay {
  --vbox-tools-color: #fff;
  --vbox-title-background: #101010;
  --vbox-title-width: "auto";
  --vbox-title-radius: 0;
  --vbox-share-background: #101010;
  --vbox-share-width: "auto";
  --vbox-share-radius: 0;
  --vbox-padding: 0;

  display: -webkit-flex;
  display: flex;
  position: fixed;
  z-index: 999999;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  -webkit-flex-direction: column;
  flex-direction: column;
  -webkit-align-items: center;
  align-items: center;
  -webkit-justify-content: center;
  justify-content: center;

  --sk-size: 40px;
  --sk-color: #333;
}
.vbox-overlay *,
.vbox-overlay :after,
.vbox-overlay :before {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-backface-visibility: hidden;
}
.vbox-overlay * {
  -webkit-backface-visibility: visible;
  backface-visibility: visible;
}

.vbox-share,
.vbox-title {
  position: fixed;
  z-index: 98;
  margin: 0 auto;
  color: var(--vbox-tools-color);
  line-height: 1;
  text-align: center;
}
.vbox-title {
  display: block;
  width: var(--vbox-title-width);
  padding: 12px 54px;
  overflow: hidden;
  border-radius: var(--vbox-title-radius);
  background-color: var(--vbox-title-background);
  font-size: 12px;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.vbox-share {
  width: var(--vbox-share-width);
  padding: 0 .35em;
  border-radius: var(--vbox-share-radius);
  background-color: var(--vbox-share-background);
  font-size: 24px;
}
.vbox-link-btn,
button.vbox-link-btn,
button.vbox-link-btn:active,
button.vbox-link-btn:focus,
button.vbox-link-btn:hover {
  display: inline-block;
  padding: 6px 12px;
  border: none !important;
  outline: 0;
  background: 0 0 !important;
  box-shadow: none !important;
  color: inherit !important;
  cursor: pointer;
}
.vbox-share a {
  display: inline-block;
  padding: 6px 12px;
  color: inherit !important;
}
.vbox-share svg {
  z-index: 10;
  vertical-align: middle;
}
.vbox-close {
  display: flex;
  position: fixed;
  z-index: 99;
  top: 0;
  right: 0;
  align-items: center;
  padding: 6px 15px;
  transition: opacity .2s;
  border: 0;
  opacity: .8;
  color: #000;
  color: var(--vbox-tools-color);
  font-size: 24px;
  cursor: pointer;
}
.vbox-close:hover {
  opacity: 1;
}
.vbox-left-corner {
  display: flex;
  position: fixed;
  z-index: 99;
  top: 0;
  left: 0;
  align-items: center;
  overflow: hidden;
  color: var(--vbox-tools-color);
  font-size: 12px;
  line-height: 1;
  cursor: pointer;
}
.vbox-num {
  display: inline-block;
  padding: 12px 15px;
}
.vbox-left {
  left: 0;
}
.vbox-right {
  right: 0;
}
.vbox-top {
  top: 0;
}
.vbox-bottom {
  bottom: 0;
}
.vbox-next,
.vbox-prev {
  display: block;
  position: fixed;
  z-index: 99;
  top: 50%;
  width: 45px;
  height: 45px;
  margin-top: -15px;
  overflow: hidden;
  transition: opacity .2s;
  opacity: .8;
  cursor: pointer;
}
.vbox-prev {
  left: 15px;
}
.vbox-next {
  right: 15px;
}
.vbox-next:hover,
.vbox-prev:hover {
  opacity: 1;
}
.vbox-next span,
.vbox-prev span {
  display: block;
  position: relative;
  position: absolute;
  top: 8px;
  width: 20px;
  height: 20px;
  border: 2px solid transparent;
  border-top-color: var(--vbox-tools-color);
  border-right-color: var(--vbox-tools-color);
  text-indent: -100px;
}

.vbox-prev span {
  left: 10px;
  -ms-transform: rotate(-135deg);
  -webkit-transform: rotate(-135deg);
  transform: rotate(-135deg);
}
.vbox-next span {
  right: 10px;
  -ms-transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}
.vbox-inline,
.venoratio {
  position: relative;
  width: 100%;
  margin: 0 auto;
}
.venoratio::before {
  content: "";
  display: block;
  padding-top: var(--vb-aspect-ratio);
}
.venoratio > * {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: var(--vbox-padding);
}
.venoratio-1x1 {
  --vb-aspect-ratio: 100%;
}
.venoratio-4x3 {
  --vb-aspect-ratio: calc(3 / 4 * 100%);
}
.venoratio-16x9 {
  --vb-aspect-ratio: calc(9 / 16 * 100%);
}
.venoratio-21x9 {
  --vb-aspect-ratio: calc(9 / 21 * 100%);
}
.venoratio-full {
  --vb-aspect-ratio: calc(100vh - 100px);
}
.vbox-child.vbox-inline,
.vbox-child.venoratio {
  max-width: 100%;
}
.vbox-open {
  overflow: hidden;
}
.vbox-container {
  position: absolute;
  z-index: 20;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  max-height: 100%;
  padding: 30px 0;
  overflow-x: hidden;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}

.vbox-content {
  display: flex;
  position: relative;
  align-items: center;
  justify-content: center;
  width: 100%;
  min-height: 100%;
  padding: 20px 4%;
  overflow: hidden;
  opacity: 0;
  text-align: center;
}
.vbox-container img {
  max-width: 100%;
  height: auto;
}
.vbox-child {
  max-width: 100%;
  padding: var(--vbox-padding);
  box-shadow: 0 0 12px rgba(0, 0, 0, .19), 0 6px 6px rgba(0, 0, 0, .23);
  text-align: initial;
}
.vbox-child img {
  display: block;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -o-user-select: none;
  user-select: none;
}
.vbox-grab .vbox-child img {
  cursor: grab;
}
.vbox-child > iframe {
  border: none !important;
}
.vbox-content.swipe-left {
  margin-left: -200px !important;
}
.vbox-content.swipe-right {
  margin-left: 200px !important;
}
.vbox-preloader {
  -webkit-transform: translateZ(0);
  -moz-transform: translateZ(0);
  -o-transform: translateZ(0);
  transform: translateZ(0);
}
.vbox-preloader .vbox-preloader-inner {
  transition: opacity .2s;
  opacity: 1;
}
.vbox-hidden {
  display: none;
}
.vbox-preloader.vbox-hidden .vbox-preloader-inner {
  opacity: 0;
}
.vbox-backdrop {
  position: absolute;
  z-index: 0;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  -webkit-transform: translateZ(-1px);
  -moz-transform: translateZ(-1px);
  -o-transform: translateZ(-1px);
  transform: translateZ(-1px);
}
.vbox-tooltip {
  display: inline-block;
  position: relative;
}
.vbox-tooltip .vbox-tooltip-text {
  visibility: hidden;
  position: absolute;
  z-index: 1;
  bottom: 100%;
  left: 0;
  margin-bottom: 2px;
  padding: 0;
  transition: opacity .3s;
  opacity: 0;
  color: #fff;
  font-family: sans-serif;
  text-align: center;
}
.vbox-top .vbox-tooltip .vbox-tooltip-text {
  top: 100%;
  bottom: auto;
  margin-top: 2px;
  margin-bottom: 0;
}
.vbox-tooltip-inner {
  padding: 5px 10px;
  border-radius: 6px;
  background-color: rgba(0, 0, 0, .9);
  font-size: 10px;
}
.vbox-tooltip:hover .vbox-tooltip-text {
  visibility: visible;
  opacity: 1;
}

.sk-center {
  margin: auto;
}
.sk-plane {
  width: var(--sk-size);
  height: var(--sk-size);
  animation: sk-plane 1.2s infinite ease-in-out;
  background-color: var(--sk-color);
}
@keyframes sk-plane {
  0% {
    transform: perspective(120px) rotateX(0) rotateY(0);
  }
  50% {
    transform: perspective(120px) rotateX(-180.1deg) rotateY(0);
  }
  100% {
    transform: perspective(120px) rotateX(-180deg) rotateY(-179.9deg);
  }
}
.sk-chase {
  position: relative;
  width: var(--sk-size);
  height: var(--sk-size);
  animation: sk-chase 2.5s infinite linear both;
}
.sk-chase-dot {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  animation: sk-chase-dot 2s infinite ease-in-out both;
}
.sk-chase-dot:before {
  content: "";
  display: block;
  width: 25%;
  height: 25%;
  animation: sk-chase-dot-before 2s infinite ease-in-out both;
  border-radius: 100%;
  background-color: var(--sk-color);
}
.sk-chase-dot:nth-child(1) {
  animation-delay: -1.1s;
}
.sk-chase-dot:nth-child(2) {
  animation-delay: -1s;
}
.sk-chase-dot:nth-child(3) {
  animation-delay: -.9s;
}
.sk-chase-dot:nth-child(4) {
  animation-delay: -.8s;
}
.sk-chase-dot:nth-child(5) {
  animation-delay: -.7s;
}
.sk-chase-dot:nth-child(6) {
  animation-delay: -.6s;
}
.sk-chase-dot:nth-child(1):before {
  animation-delay: -1.1s;
}
.sk-chase-dot:nth-child(2):before {
  animation-delay: -1s;
}
.sk-chase-dot:nth-child(3):before {
  animation-delay: -.9s;
}
.sk-chase-dot:nth-child(4):before {
  animation-delay: -.8s;
}
.sk-chase-dot:nth-child(5):before {
  animation-delay: -.7s;
}
.sk-chase-dot:nth-child(6):before {
  animation-delay: -.6s;
}
@keyframes sk-chase {
  100% {
    transform: rotate(360deg);
  }
}
@keyframes sk-chase-dot {
  100%,
  80% {
    transform: rotate(360deg);
  }
}
@keyframes sk-chase-dot-before {
  50% {
    transform: scale(.4);
  }
  0%,
  100% {
    transform: scale(1);
  }
}
.sk-bounce {
