@charset 'UTF-8';
// @use "../inc/mixin" as *;
@use "../inc/color-book" as *;

// -----------------------------------
//  Layout Settings
//------------------------------------

// Content
$content-width: 1080px !default;

// 
$breakpoints: (
  xxs: 0,
  xs: 360px,
  sm: 576px,
  md: 768px,
  lg: 992px,
  lx: 1200px,
  xxl: 1450px,
  xxxl: 1680px,
) !default;

//====================================
//  Color Setings
//====================================

// 
$colorBase: #ddd;

// 
$colorMain: #006edb;

// 
$colorSub: #f5f3eb;

//
$colorAccent: #a68021;

// 
$colorWhite: #fff;

// 
$colorBlack: #171717;

// 
$colorLightGray: #f5f5f5;

// 
$colorGray: #919191;

// 
$colorDarkGray: #808080;

// 
$colorBeige: #e7e7e1;

// 
$colorLightBeige: #fcfbf5;

// 
$primary-text-color: #2b2b2b;

// 
$secondary-text-color: $md-blue-grey-500;

// 
$hint-text-color: $md-blue-grey-300;

// 
$link-text-color: $md-cyan-500;

// 
$hover-text-color: $md-cyan-300;

//====================================
//  Typeface
//====================================

// @font-face {
//   font-family: 'bourtonLine';
//   src: url('/assets/fonts/bourtonLineM.woff') format('woff');
//   font-weight: 400;
//   font-style: normal;
// }

//$font-set-primary : set-font-family(1) !default;
$font-set-primary: "M PLUS 1p", "Noto Sans JP", "", "Yu Gothic", "", "YuGothic",
  " Pro W3", "Hiragino Kaku Gothic Pro", "Meiryo UI", "", Meiryo, sans-serif;
// $font-set-primary: "Noto Serif JP", "", "Yu Mincho", YuMincho, "Hiragino Mincho Pro", serif;
$font-set-serif: "Shippori Mincho", serif;
$font-set-en: "bourtonLine";

// 1 : Roboto, Noto sans
// 2 : Baskerville, 
// 3 : Open Sans, Noto sans
// 4 : Lato, Noto sans
// 5 : Roboto, 
// 6 : Baskerville, Noto sans
// 7 : Noto Sans

// $size: 14px;
$base: 16px;

//====================================
//  cubic-bezier
//====================================
$easing-opera: cubic-bezier(.82, 0, .16, 1);
// $easing-shibata: cubic-bezier(0.22, 0, 0.53, 1);
$easing-heart: cubic-bezier(.22, 0, .53, 1);

//====================================
//  common easing
//====================================
$easeInSine: cubic-bezier(.47, 0, .745, .715);
$easeOutSine: cubic-bezier(.39, .575, .565, 1);
$easeInOutSine: cubic-bezier(.445, .05, .55, .95);
$easeInQuad: cubic-bezier(.55, .085, .68, .53);
$easeOutQuad: cubic-bezier(.25, .46, .45, .94);
$easeInOutQuad: cubic-bezier(.455, .03, .515, .955);

$easeInCubic: cubic-bezier(.55, .055, .675, .19);
$easeOutCubic: cubic-bezier(.215, .61, .355, 1);
$easeInOutCubic: cubic-bezier(.645, .045, .355, 1);
$easeInQuart: cubic-bezier(.895, .03, .685, .22);
$easeOutQuart: cubic-bezier(.165, .84, .44, 1);
$easeInOutQuart: cubic-bezier(.77, 0, .175, 1);

$easeInQuint: cubic-bezier(.755, .05, .855, .06);
$easeOutQuint: cubic-bezier(.23, 1, .32, 1);
$easeInOutQuint: cubic-bezier(.86, 0, .07, 1);
$easeInExpo: cubic-bezier(.95, .05, .795, .035);
$easeOutExpo: cubic-bezier(.19, 1, .22, 1);
$easeInOutExpo: cubic-bezier(1, 0, 0, 1);

$easeInCirc: cubic-bezier(.6, .04, .98, .335);
$easeOutCirc: cubic-bezier(.075, .82, .165, 1);
$easeInOutCirc: cubic-bezier(.785, .135, .15, .86);
$easeInBack: cubic-bezier(.6, -.28, .735, .045);
$easeOutBack: cubic-bezier(.175, .885, .32, 1.275);
$easeInOutBack: cubic-bezier(.68, -.55, .265, 1.55);
